body {
  background-color: aqua;
  display: flex;
  justify-content: center;
  align-items: center;
}

#quote-box {
  text-align: center;
  width: 450px;
  height: auto;
  background-color: white;
  border: 2px solid black;
  padding: 40px 40px;
  margin-top: 80px;
}
#quote-box #text {
  background-color: beige;
}
#quote-box #author {
  justify-self: right;
}

#header {
  position: absolute;
  background-color: gray;
  text-align: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100px;
}